/* Navigation */

nav ul {
	margin-top: -4px;
	text-align: right;
	
}
nav > ul > li {
	display: inline-block;
	margin-right: -1px;
	position: relative;
	border-radius: 5px;
	
}
nav ul li a {
	display: inline-block;
	color: #666;
	font-size: 15px;
	position: relative;
	padding: 40px 25px 35px;
	font-weight: lighter !important;
}
nav ul li a:before {
	content: "";
    background: rgba(255, 255, 255, 0.1);
    width: 100%;
    height: 43px;
    border-radius: 43px;
    position: absolute;
    left: 0;
    top: calc(50% - 21px);
    opacity: 0;
    transition: all 0.3s;
	z-index: -1;
	
}
nav ul li a.active:before{
	opacity: 1;
}
nav ul li a:hover:before{
	opacity: 1;
}
nav ul li:hover > ul {
	opacity: 1;
	visibility: visible;
}
nav ul li:hover > ul li {
	margin-top: 0;
}
nav ul li a:hover,
nav ul li a.active {
	color: #FFA178;
	text-decoration: none;
}

nav ul ul {
	position: absolute;
	top: 104%;
	left: 0;
	width: 215px;
	-ms-box-shadow: 0 0 20px rgba(0,0,0,0.03);
	-o-box-shadow: 0 0 20px rgba(0,0,0,0.03);
	box-shadow: 0 0 20px rgba(0,0,0,0.03);
	opacity: 0;
	visibility: hidden;
	border-radius: 15px;
	z-index: 99;
	text-align: left;
}
nav ul ul li {
	padding: 10px 15px;
	border-radius: 20px;
	background-color: #fff;
	border-bottom: 1px solid #ebebeb;
	margin-top: 15px;
	position: relative;
	transition: all .5s;
	position: relative;
}
nav ul ul li i{
	position: absolute;
	right: 10px;
	top: 18px;
}
nav ul ul li a{
	padding: 5px;
	color: #333;
}
nav ul ul li:last-child {
	border-bottom: 0;
}
nav ul ul ul {
	left: 100%;
	top: 4px;
	transition: all .5s;
}


