    margin-top: 25px;
}

.progress-area span {
    color: #f7797d;
    font-size: 25px;
    display: block;
    text-align: center;
    font-weight: 700;
}

.progress {
    width: 150px;
    height: 150px!important;
    line-height: 150px;
    background: none;
    margin: 0 auto;
    box-shadow: none;
    position: relative;
    overflow: unset;
    border-radius: 50%!important;
    margin-bottom: 20px;
}

.progress:after {
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 20px solid #ebebeb;
    position: absolute;
    top: 0;
    left: 0;
}

.progress>span {
    width: 50%;
    height: 100%;
    overflow: hidden;
    position: absolute;
    top: 0;
    z-index: 1;
}

.progress .progress-left {
    left: 0;
}

.progress .progress-bar {
    width: 100%;
    height: 100%;
    background: none;
    border-width: 21px;
    border-style: solid;
    position: absolute;
    top: 0;
}

.progress .progress-left .progress-bar {
    left: 100%;
    border-top-right-radius: 80px;
    border-bottom-right-radius: 80px;
    border-left: 0;
    -webkit-transform-origin: center left;
    transform-origin: center left;
}

.progress .progress-right {
    right: 0;
}

.progress .progress-right .progress-bar {
    left: -100%;
    border-top-left-radius: 80px;
    border-bottom-left-radius: 80px;
    border-right: 0;
    -webkit-transform-origin: center right;
    transform-origin: center right;
    -webkit-animation: loading-1 1.8s linear forwards;
            animation: loading-1 1.8s linear forwards;
}

.progress .progress-value {
    width: 90%;
    height: 90%;
    border-radius: 50%; 
    background: #fff;
    font-size: 24px;
    color: #333;
    line-height: 145px;
    text-align: center;
    position: absolute;
    top: 5%;
    left: 5%;
    font-weight: 700;
}

.progress.blue .progress-left .progress-bar {
    -webkit-animation: loading-2 1.5s linear forwards 1.8s;
            animation: loading-2 1.5s linear forwards 1.8s;
}

.progress.s1 .progress-right .progress-bar {
    -webkit-animation: loading-2 1s linear forwards 1.8s;
            animation: loading-2 1s linear forwards 1.8s;
}

.progress.s1 .progress-bar {
    border-color: #0dc2f8;
}
.progress.s1:after{
    border-color: rgba(182,228,254, .2);
}

.progress.s2 .progress-bar {
    border-color: #ff8f84;
}
.progress.s2:after {
    border-color: #ffdadc;
}

.progress.s3 .progress-bar {
    border-color: #8379fe;
}

.progress.s3:after{
    border-color: #d6d3ff;
}

.progress.s3 .progress-right .progress-bar {
    -webkit-animation: loading-3 1s linear forwards 1.8s;
            animation: loading-3 1s linear forwards 1.8s;
}

@-webkit-keyframes loading-1 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(180deg);
        transform: rotate(180deg);
    }
}

@keyframes loading-1 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(180deg);
        transform: rotate(180deg);
    }
}

@-webkit-keyframes loading-2 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(80deg);
        transform: rotate(80deg);
    }
}

@keyframes loading-2 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(80deg);
        transform: rotate(80deg);
    }
}

@-webkit-keyframes loading-3 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(110deg);
        transform: rotate(110deg);
    }
}

@keyframes loading-3 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(110deg);
        transform: rotate(110deg);
    }
}

@-webkit-keyframes loading-4 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(46deg);
        transform: rotate(46deg);
    }
}

@keyframes loading-4 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(46deg);
        transform: rotate(46deg);
    }
}

@-webkit-keyframes loading-5 {
    0% {
        -webkit-transform: rotate(0deg);
