/*--------------------------------------------------------------
#0.1    Theme Reset Style
--------------------------------------------------------------*/
html {
    font-size: 16px;
}

body {
    font-family: 'DM Sans'!important;
    background-color: #fff;
    font-size: 16px;
    font-size: 1rem;
    color: #525252;
    -webkit-font-smoothing: antialiased;
    overflow-x: hidden;
}

.App{
    overflow-x: hidden;
}

@media (max-width: 767px) {
    body {
        font-size: 14px;
    }
}

p {
    color: #777777;
    line-height: 1.8em;
    font-size: 16px;
    font-weight: 400;
}

h1,
h2 {
    font-family: 'DM Sans';
    font-weight: bolder;
    color: #FB9C7D;
}
h1,
h2, 
h3 {
   margin-top: 20px;
}

h3,
h4,
h5,
h6{
    font-family: 'Lato';
}

ul {
    padding-left: 0;
    margin: 0;
    list-style: none;
}

a {
    text-decoration: none;
    transition: all 0.2s;
}

a:hover {
    text-decoration: none!important;
}

img {
    max-width: 100%;
}

@media(min-width: 1200px){
  .container{
    width: 1200px;
    max-width: 100%;
 }
}

/*--------------------------------------------------------------
#0.2    Global Elements
--------------------------------------------------------------*/
.page-wrapper {
    position: relative;
    overflow: hidden;
}

.wow {
    visibility: hidden;
}

.fi:before {
    margin: 0;
}

.section-padding {
    padding: 120px 0;
}

@media (max-width: 991px) {
    .section-padding {
        padding: 90px 0;
    }
}

@media (max-width: 767px) {
    .section-padding {
        padding: 80px 0;
    }
}

/*** back to top **/
.back-to-top {
  background-color: rgba(255, 191, 124, 0.7);
  width: 45px;
  height: 45px;
  line-height: 45px;
  border: 2px solid #f7797d;
  border-radius: 50%;
  text-align: center;
  display: none;
  position: fixed;
  z-index: 9999999;
  right: 15px;
  bottom: 15px;
  transition: all 0.2s;
}

@media (max-width: 991px) {
  .back-to-top {
    width: 35px;
    height: 35px;
    line-height: 35px;
  }
}

.back-to-top:hover {
  background-color: #f56065;
}

.back-to-top i {
  font-size: 22px;
  font-size: 1.46667rem;
  color: #fff;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

/*** contact form error handling ***/
.contact-validation-active .error-handling-messages {
    width: 100% !important;
    margin-top: 15px !important;
}

.contact-validation-active label.error {
    color: red;
    font-size: 0.875rem;
    font-weight: normal;
    margin: 5px 0 0 0;
    text-align: left;
    display: block;
}

.contact-validation-active #loader {
    display: none;
    margin-top: 10px;
}

.contact-validation-active #loader i {
    font-size: 30px;
    font-size: 1.875rem;
    color: #FB9C7D;
    display: inline-block;
    -webkit-animation: rotating linear 2s infinite;
    animation: rotating linear 2s infinite;
}

.contact-validation-active #success,
.contact-validation-active #error {
    width: 100%;
    color: #fff;
    padding: 5px 10px;
    font-size: 16px;
    text-align: center;
    display: none;
}

@media (max-width: 767px) {

    .contact-validation-active #success,
    .contact-validation-active #error {
        font-size: 15px;
    }
}

.contact-validation-active #success {
    background-color: #02c754;
    border-left: 5px solid green;
    margin-bottom: 5px;
}

.contact-validation-active #error {
    background-color: #ff1a1a;
    border-left: 5px solid red;
}

@-webkit-keyframes rotating {
    from {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes rotating {
    from {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

.rotating {
    -webkit-animation: rotating 5s alternate infinite;
    animation: rotating 5s alternate infinite;
}

/** for popup image ***/
.mfp-wrap {
    background-color: rgba(0, 0, 0, 0.9);
    z-index: 99999;
}

.mfp-with-zoom .mfp-container,
.mfp-with-zoom.mfp-bg {
    opacity: 0;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    transition: all 0.3s ease-out;
}

.mfp-with-zoom.mfp-ready .mfp-container {
    opacity: 1;
}

.mfp-with-zoom.mfp-ready.mfp-bg {
    opacity: 0.8;
}

.mfp-with-zoom.mfp-removing .mfp-container,
.mfp-with-zoom.mfp-removing.mfp-bg {
    opacity: 0;
}

/*** for fancybox video ***/
.fancybox-overlay {
    background: rgba(0, 0, 0, 0.9);
    z-index: 9999 !important;
}

.fancybox-wrap {
    z-index: 99999 !important;
}

.wpo-section-title {
    text-align: center;
    position: relative;
    max-width: 500px;
    margin: auto;
    margin-bottom: 70px;
}



.wpo-section-title span {
    font-size: 18px;
    font-weight: 600;
    display: block;
    margin-bottom: 10px;
    background: -webkit-linear-gradient(left, #000000, #000);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

.wpo-section-title h2 {
    font-size: 40px;
    font-weight: 700;
    margin: 0;
    display: inline-block;
    position: relative;
    line-height: 60px;
}


@media (max-width: 767px) {
    .wpo-section-title h2 {
        font-size: 25px;
        line-height: 35px;
    }
}

.theme-btn,
.theme-btn-s2,
.theme-btn-s4,
.theme-btn-s3 {
    background: linear-gradient(to left, #f7797d, #FFBF7C);
    background: linear-gradient(to left, #f7797d, #FFBF7C);
    color: #fff;
    display: inline-block;
    font-weight: 500;
    padding: 13px 30px;
    border: 0;
    border-radius: 55px;
    text-transform: capitalize;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    z-index: 1;
    position: relative;
    line-height: 21px;
}

.theme-btn:after,
.theme-btn-s3:after,
.theme-btn-s4:after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 55px;
    background: linear-gradient(to left, #FFBF7C, #f7797d);
    transition: opacity 0.5s ease-out;
    opacity: 0;
    z-index: -1;
}

.theme-btn:hover:after,
.theme-btn-s3:hover:after,
.theme-btn-s4:hover:after,
.view-cart-btn:hover:after {
    opacity: 1;
}

@media (max-width: 991px) {

    .theme-btn,
    .theme-btn-s2,
    .theme-btn-s4,
    .theme-btn-s3 {
        font-size: 14px;
        font-size: 0.875rem;
        padding: 10px 25px;
        line-height: 26px;
    }
}

.theme-btn-s3 {
    border-radius: 55px;
    box-shadow: none;
}

.theme-btn-s3:after {
    border-radius: 55px;
}

.theme-btn:hover,
.theme-btn-s2:hover,
.theme-btn-s4:hover,
.theme-btn-s3:hover,
.theme-btn:focus,
.theme-btn-s2:focus,
.theme-btn-s4:focus,
.theme-btn-s3:focus,
.theme-btn:active,
.theme-btn-s2:active,
.theme-btn-s4:active,
.theme-btn-s3:active {
    background-color: #f7797d;
    color: #fff;
}

.theme-btn-s4 {
    font-weight: 400;
    color: #666666;
    transition: all .3s;
    -moz-transition: all .3s;
    -webkit-transition: all .3s;
}

.theme-btn-s2 {
    border: none;
    background: none;
    border: 1px solid #fff;
    color: #fff;
}

.theme-btn-s2:hover,
.theme-btn-s4:hover,
.theme-btn-s2:focus,
.theme-btn-s4:focus,
.theme-btn-s2:active,
.theme-btn-s4:active {
    background-position: 100% !important;
    color: #fff;
}

.theme-btn-s3 {
    padding: 16px 30px 14px;
}

@media (max-width: 991px) {
    .theme-btn-s3 {
        font-size: 14px;
        font-size: 0.875rem;
        padding: 10px 25px;
    }
}

.theme-btn-s4 {
    padding: 15px 48px;
    border-radius: 55px;
}

@media (max-width: 991px) {
    .theme-btn-s4 {
        font-size: 14px;
        font-size: 0.875rem;
        padding: 10px 25px;
    }
}

.view-cart-btn {
    display: block;
}

.social-links {
    overflow: hidden;
}

.social-links li {
    float: left;
    width: 35px;
    height: 35px;
    margin-right: 1px;
}

.social-links li a {
    background-color: #FFBF7C;
    width: 35px;
    height: 35px;
    line-height: 35px;
    display: block;
    color: #fff;
    text-align: center;
